.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.container {
  width: 100vw;
  height: 100vh;  
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center
}

.box {
  width: 500px;
  height: 300px;
  background: #fff;
}

.buttonStyle {
  width: 100%;
  height: 200px !important;    
  font-size: 18px !important;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
